<template>
  <div id="app">
    <div class="title"><i class="el-icon-document">文件名修改系统</i></div>
    <div class="dir">
      <i class="el-icon-edit"> 根文件地址：</i>
      <el-input style="width: 60%"  ref="kk"
              placeholder="请输入内容"
              v-model="root"
              clearable>
      </el-input>
    </div>
    <template>
      <el-tabs v-model="activeName" style="margin: 0 50px">
        <el-tab-pane label="简单模式" name="first">
          <div class="inputLine">
            <div>
              <label for="a">以xx开头:</label>
              <input v-model="mode" id="a" type="radio" value="1">
              <label for="b">以xx结尾:</label>
              <input v-model="mode"  id="b" type="radio" value="2">
              <label for="c">包含xx内容:</label>
              <input v-model="mode"  id="c" type="radio" value="3">

              <el-input
                      placeholder="请输入内容"
                      v-model="first"
                      clearable>
              </el-input>
            </div>
          </div>

        </el-tab-pane>
        <el-tab-pane label="正则模式" name="second">
          <div class="inputLine" style="display: inline-block">
            <div>
              <span>正则(Golang):</span>
              <el-input
                      placeholder="请输入内容 如: ^[1-8]+.txt$ "
                      v-model="second"
                      clearable>
              </el-input>
            </div>
          </div>

        </el-tab-pane>
      </el-tabs>
    </template>
    <button @click="search">搜 索</button>
    <fieldset>
      <legend><b style="color: #1e8bfe">批量修改文件名</b></legend>
      <div class="rename">
        <label for="aa">添加前缀:</label>
        <input v-model="reNameMode" id="aa" type="radio" value="prefix">
        <label for="bb">去除文件名中包含的字段:</label>
        <input v-model="reNameMode"  id="bb" type="radio" value="del">
        <el-input ref="k" style="width: 200px;"
                  placeholder="请输入内容"
                  v-model="reNameStr"
                  clearable>
        </el-input>
        <button @click="rename">提交修改</button>
      </div>

    </fieldset>


    <div class="res"  v-loading="loading">
      <ol>
        <li v-for="v,i in reslist" :key="i">{{v}}</li>
      </ol>
    </div>

  </div>
</template>
<script>
    import Fly from 'flyio/dist/npm/fly'
    let fly=new Fly()
    fly.interceptors.request.use((request)=>{
        request.headers["Content-Type"]="application/x-www-form-urlencoded";
        return request;
    })

  export default {
      name:"App",
      data(){
          return{
              activeName: 'first',
              first:"",
              second:'',
              mode:1,
              root:'',
              reslist:[],
              reNameMode:"prefix",
              reNameStr:"",
              loading:false
          }
      },
      methods:{
          rename(){
              if(!this.reNameStr){
                  alert("请先填写修改内容")
                  this.$refs.k.focus()
                  return
              }
              let o={}
              if(this.reNameMode=="del"){
                  o.del=this.reNameStr
              }else o.prefix=this.reNameStr

              this.loading=true
              fly.post(`http://localhost:8888/set`,o)
                  .then(d=>{
                      console.log(d);
                      this.search()
                      this.loading=false
                  }).catch(e=>{
                  this.loading=false
                  console.log(e);
              })

          },
          search(){
              if(!this.root){
                  alert("请填写根目录!");
                  this.$refs.kk.focus()
                  return
              }
              var regStr;
              if(this.activeName==="first"){//简单模式
                  if(!this.first){
                      regStr=`.*`
                  }else {
                      if(this.mode==1){
                          regStr=`^${this.first}.*?`
                      }else if(this.mode==2){
                          regStr=`.*?${this.first}$`
                      }else if(this.mode==3) {
                          regStr=`.*?${this.first}.*?`
                      }
                  }

              }else {//正则模式
                  if(!this.second){
                      regStr=`.*`
                  }else regStr=this.second

              }
              console.log('regStr: ',regStr)

              this.loading=true
              fly.get("http://localhost:8888/get",{
                  regStr,
                  root:this.root
              }).then(d=>{
                  if(d.data=="根目录不存在"){
                      alert("根目录不存在,请重新填写!!")
                  }else {
                      this.reslist=JSON.parse(d.data)
                      // console.log(77,this.reslist)
                  }
                  this.loading=false
              }).catch(e=>{
                  this.loading=false
                  console.log("错误:",e)
              })
          }
      }
  }
</script>
